#about {
  box-shadow: inset 0px 0px 12px 0px rgba(0, 0, 0, 0.1);
  background: #ecf5ff;
  margin-top: 120px;
  padding-top: 70px;
  padding-bottom: 130px;
}
.about_section {
  text-align: center;
  padding: 1rem 2rem;
}
.text_about {
  font-size: 3rem;
  line-height: 1.4;
  margin: 1.5rem 0;
  color: #469272;
  font-weight: 500;
  position: relative;
  bottom: 1rem;
}
.about_container {
  display: flex;
  justify-content: space-between;
  gap: 80px;
}
.img_about {
  height: 500px;
}
.about_box {
  border-radius: 5px;
  justify-content: center;
  align-items: flex-start;
  text-align: justify;
  font-style: italic;
  font-size: 20px;
}
.about_box_text {
  margin-top: 10px;
  font-size: 20px;
}

@media screen and (max-width: 1200px) {
  .about_container {
    flex-direction: column;
  }
  .img_about {
    height: 300px;
  }
}

@media screen and (max-width: 380px) {
  #about {
    margin-top: 45px;
    padding-top: 20px;
    padding-bottom: 0px;
    width: 109%;
  }
  .about_section {
    padding: 1rem 1rem;
  }
  .text_about {
    margin: 1.5rem 0;
  }
}

#about .about_section .about_container .about_box {
  padding: 3rem;
  margin-top: 50px;
}
#about .about_section .about_container {
  gap: 50px;
}
